				<li>
					<img src="../assets/icon5.png" />
					<p>同分去向</p>
				</li>
				<li>
					<img src="../assets/icon6.png" />
					<p>批次线</p>
				</li>
			</ul>
		
			<div class="advice_box">
				<div class="header">
					<div class="vertical_line"></div>
					<span class="font_color header_title">专家咨询（限免）</span>
					<div class="right_box" >
						<span >更多</span>
						<i   />
					</div>
				</div>

				<div class="swiper-container gallery-thumbs swiper-container-horizontal">
					<div class="swiper-wrapper">
						<div class="swiper-slide swiper-slide-next" style="margin-right: 10px;">
							<div class="swiper-top">

							</div>
							<p class="teacher_name">赵坤鹏</p>
							<p class="teacher_type">金牌规划师</p>
