</template>

<script>
import Swiper from '../../static/swiper.min.js'
let galleryThumbs
export default {
  name: 'Home',
   data () {  
    return {  
      transitionName: 'slide-left'  
    }  
  },
  mounted () {
    this.lunbo();
    
  },
  methods: {
    lunbo () {
      galleryThumbs = new Swiper('.gallery-thumbs', {
        spaceBetween: -10,
		      initialSlide: 1,
			  centeredSlides: true,
			  slidesPerView: "auto",
			  touchRatio: 0.3,
			  slideToClickedSlide: true,
			  autoplayDisableOnInteraction: true,
				coverflow: {
				  rotate: 0,
				  stretch: 0,
				  depth: 100,
				  modifier: 1,
				  lideShadows: false
		      },
       onProgress: function(a) {
var b, c, d, scale, es;
for(b = 0; b < a.slides.length; b++) c = a.slides[b], d = c.progress, scale = 1 - Math.min(Math.abs(0.2 * d), 1), es = c.style, es.webkitTransform = es.MsTransform = es.msTransform = es.MozTransform = es.OTransform = es.transform = 'translate3d(0px,0px,' + (-Math.abs(150 * d)) + 'px)'
}
})
},
  stopPlay () {
    galleryThumbs.stopAutoplay()
  },
  play () {
    galleryThumbs.startAutoplay()
  }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import "../assets/common.scss";
	@import url("../assets/swiper.min.css");
	@import "../assets/home.scss";
	
	
	
	
</style>


