<template>
<el-container>
 <el-header style="height: 0.88rem;">
  	<i class="back" v-on:click="back()"></i>
  	<span>预估成绩</span>
  </el-header>
   <el-main>
   	<div class="show_box">
   		<div class="select_btn" v-on:click="choose()">
   			 <i class="pull_down"></i>
   			<li v-for="(chooseData,index) in chooseData" @click="choose_type(index,chooseData)">{{chooseData}}</li>
   			
   		</div>
   		<div class="mark_box">
   			
   			<div id="mark_model" v-if="data == '分数' " @click="openMessage()">{{mark}}</div>
   			<div id="mark_model" v-if="data == '名次' " @click="openMessage()">{{ranking}}</div>
   			<span>分</span>
   		</div>
   	</div>
   	<div class="line"></div>
   	<div class="select_box" @click="placeShow">
   		<span>考试地区</span>
   		<div class="select">{{fromPlace}}
   		<i class="select_icon"></i>
   		</div>
   	</div>
   	<div class="select_box" @click="subShow">
   		<span>文理科</span>
   		 <div class="select">{{subject}}
   		 <i class="select_icon"></i>
   		 </div>
   	</div>
   	<button class="confirm_btn" @click="comfirm">
   		确定
   		
   	</button>
   
   	<div id="place" v-bind:style="maskStyle" class="mask">
   			<div class="place_box">
   				 <el-header style="height: 0.88rem;">
  	  <span class="mt-btn" style="float: left;" @click="closePlace()">取消</span>
  	<span>考试地区</span>
  	<span class="mt-btn" style="float: right;" @click="comfirmPlace()">确定</span>
  </el-header>
   		 <mt-picker :slots="slots"  @change="placeChange" ref="place"></mt-picker>  
   	</div>
   	</div>
   
   	<div id="subject" v-bind:style="maskStyle" class="mask">
   			<div class="place_box">
   				 <el-header style="height: 0.88rem;">
  	  <span class="mt-btn" style="float: left;" @click="closeSub()">取消</span>
  	<span>考试地区</span>
  	<span class="mt-btn" style="float: right;" @click="comfirmSub()">确定</span>
  </el-header>
   		 <mt-picker :slots="sub"  @change="subChange" ref="sub"></mt-picker>  
   	</div>
   	</div>
   	
       </el-main>
</el-container>
</template>


<script>
	
	var height=document.documentElement.clientHeight*2/100+"rem";

	import { MessageBox } from 'mint-ui';
	import { Toast } from 'mint-ui';
	import Zepto from '../../static/zepto.min.js'
	
	
	export default{
		name:'Mark',
		data:function(){
			return {
				flag:"true",
				chooseData:["高考分数","全省排行"],
				data:"分数",
				mark:this.message.mark,
				ranking:this.message.ranking,
				fromPlace:this.message.fromPlace,
				subject:this.message.subject,
				slots:[{values: ['广东', '北京', '上海', '天津', '湖南']}],
				maskStyle:{
					height:height
				},
				sub:[{values:["文科","理科"]}],
				transfer:{
					
					subject:"",
					fromPlace:""
				}
			}
		},
		mounted(){
			
			if(this.message.subject!=""){
				this.$refs.sub.setSlotValue(0,this.message.subject)
			}
			if(this.message.fromPlace!=""){
				this.$refs.place.setSlotValue(0,this.message.fromPlace)
			}
		},
		
		
		methods:{
			back (){
				this.$router.go(-1);
				this.$router.isBack = true;
			},
			placeShow(){
				$("#place").fadeIn()
			},
			subShow(){
				$("#subject").fadeIn()
			},
			choose(){
				if(this.flag){
					$(".select_btn").animate({"height":"1.41rem"})
					$(".pull_down").animate({"transform":"rotate(180deg)"})
				}else{
					$(".select_btn").animate({"height":"0.7rem"});
					$(".pull_down").animate({"transform":"rotate(0deg)"})
				}

				this.flag=!this.flag
			}
			,
			choose_type(index,data){
				
				if(this.flag==false){
						if(index==1){
						var temp=$('.select_btn li').eq(0).html();
					$('.select_btn li').eq(0).html($('.select_btn li').eq(1).html());
					$('.select_btn li').eq(1).html(temp);
					
					if(temp=="高考分数"){
						$(".mark_box span").html("名");
						this.data="名次"
					
					}else{
						$(".mark_box span").html("分");
						this.data="分数"
					}
					}
					
				}
				
				
			}
			,
			openMessage(){
			 if(this.data=="分数"){
			 	var html='<span style="color:black">请输入分数<span>'
			 	MessageBox.prompt("",{
			 		
			 		message:html,
			 		title:"",
			 		inputType:"number"
			 	}).then(
			 		
			 		({value,action}) => {
			 			console.log(parseInt(value))
			 			if(parseInt(value)>750||parseInt(value)<0){
			 				Toast('输入分数错误');
			 				 return;
			 			}else{
			 				this.message.mark=value;
			 			
			 			this.mark=this.message.mark
			 			}
			 		
			 			
			 		}
			 	);
			 }else{
			 	var html='<span style="color:black">请输入名次<span>'
			 	MessageBox.prompt("",{
			 		width:400,
			 		message:html,
			 		title:"",
			 		inputType:"number"
			 	}).then(
			 		
			 		({value,action}) => {
			 			if(parseInt(value)<0){
			 				Toast('输入名次错误');
			 			}else if(parseInt(value)>=10000){
			 				this.message.ranking=">9999"
			 				this.ranking=this.message.ranking
			 			}else{
			 				this.message.ranking=value;
			 			
			 			this.ranking=this.message.ranking
			 			}
			 			
			 		}
			 	);
			 }
        
			},
			placeChange(picker,values){
				
				this.transfer.fromPlace=values[0];
			},
			subChange(picker,values){
				
				this.transfer.subject=values[0];
			},
			closePlace(){
				$('#place').fadeOut()
			},
			comfirmPlace(){
				this.message.fromPlace=this.transfer.fromPlace
				this.fromPlace=this.message.fromPlace;
				$('#place').fadeOut()
			}
			,
			closeSub(){
				$('#subject').fadeOut()
			},
			comfirmSub(){
				this.message.subject=this.transfer.subject
				this.subject=this.message.subject;
				$('#subject').fadeOut()
			},
			comfirm(){
				if(this.message.mark==""){
					Toast('信息不能为空');
					return;
				}
				 else if(this.message.subject==""){
					Toast('信息不能为空');
					return;
				}
				else if(this.message.fromPlace==""){
					Toast('信息不能为空');
					return;
				}
				else if(this.message.ranking==""){
					Toast('信息不能为空');
					return;
				}else{
					this.$router.push({path:'/Wish'})
				}
				
			}
		}
	}
	
</script>

<style lang="scss" scoped>
@import "../assets/common.scss";
@import "../assets/Mark.scss";
	
</style>


