<template>
<el-container>
<el-header style="height: 0.88rem;">
  	<i class="back" v-on:click="back()"></i>
  	<span class="mid_text">职业方向</span>
  	<span class="header_right" @click="cancel">取消<span >({{pcount}})</span></span>
  </el-header>
  <el-main>
  	<div v-for="(Occarr,index) in Occarr" class="checkbox" @click="select(index)">{{Occarr}}</div>
  	
  	
  </el-main>
  </el-container>
</template>

<script>
	import { MessageBox } from 'mint-ui';
	export default{
		name:'Occupation',
		data:function(){
			return{
					Occarr:["交通输入类","交通输入类","化学类","交通输入类","交通输入类","化学类","交通输入类","交通输入类","化学类"],
					pcount:this.message.occ.length
			}                
		},
		mounted(){
			
			this.flagArr=this.flag();
			this.background();
			
		},
		
		
		
		methods:{
			back(){
				this.$router.go(-1);
				this.$router.isBack = true;
			},
			select(index){
				
			 if(this.flagArr[index]){
			 	$('.checkbox').eq(index).css({"background":"#2B94FA","color":"#FFFFFF"})
			 	this.flagArr[index]=false;
			 	var newobj={};
			 	newobj.index=index;
			 	this.message.occ.push(newobj);
			 	this.pcount=this.message.occ.length;
			 	console.log(this.message.occ)
			 }else{
			 $('.checkbox').eq(index).css({"background":"#FFFFFF","color":"#2B94FA"})	
			 	this.flagArr[index]=true
			 	for(var i=0;i<this.message.occ.length;i++){
			 		if(this.message.occ[i].index==index){
			 			console.log(this.message.occ[i])
			 			this.message.occ.splice(i,1)
			 		}
			 	}
			 	console.log(this.message.occ)
			 	this.pcount=this.message.occ.length
			 }
			},
			flag(){
				
				var len=this.Occarr.length;
				var flagArr=[];
				for(var i=0;i<len;i++){
					flagArr[i]=true;
				}
				return flagArr;
			},
			cancel(){
			MessageBox.confirm("",{
		
				message:"<span style='color:red'>你确定删除所有所选方向？</span><div style='height:0.5rem;'></div>",
				title: ""
			}).then(action => {
				$('.checkbox').each(function(i){
					$('.checkbox').eq(i).css({"background":"#FFFFFF","color":"#2B94FA"});	
				});
				this.message.occ.length=0;
				this.pcount=this.message.occ.length;
				for(var i=0;i<this.flagArr.length;i++){
					this.flagArr[i]=true
				}
				})
			},
			background(){
				for(var i=0;i<this.message.occ.length;i++){
						$('.checkbox').eq(this.message.occ[i].index).css({"background":"#2B94FA","color":"#FFFFFF"});
						this.flagArr[this.message.occ[i].index]=false;
				}
			}
		}
	}
</script>


<style lang="scss" scoped>
@import "../assets/common.scss";

</style>


