<template>
<el-container>
<el-header style="height: 0.88rem;">
  	<i class="back" v-on:click="back()"></i>
  	<span>意愿设置</span>
  </el-header>
  <el-main>
  	<div class="header" style="border-bottom: 1px solid #F0F0F0;">
					<div class="vertical_line"></div>
					<span class="font_color header_title">学校意愿</span>
				
				</div>
	<div class="select_box schoolBox" @click="schoolShow">
   		<span>学校地区</span>
   		<div class="select" style="width: 1rem;">
   			{{school}}
   		<i class="select_icon"></i>
   		</div>
   	</div>
   	<div class="select_box sexBox" @click="sexShow">
   		<span>男女比例</span>
   		<div class="select" style="width: 1rem;">
   			{{sex}}
   		<i class="select_icon"></i>
   		</div>
   	</div>
   	<div class="header" style="border-bottom: 1px solid #F0F0F0;">
					<div class="vertical_line"></div>
					<span class="font_color header_title">专业意愿</span>
				
				</div>
				<div class="select_box " @click="goPro">
   		<span>专业类别</span>
   		<div class="select" style="width: 2rem;">
   			{{ProLen}}
   		<i class="select_icon"></i>
   		</div>
   	</div>
   	<div class="select_box " @click="goOcc">
   		<span>职业方向</span>
   		<div class="select" style="width: 2rem;">
   			{{OccLen}}
   		<i class="select_icon"></i>
   		</div>
   	</div>
   	<button disabled="disabled" class="confirm_btn" @click="comfirm">
   		确定
   	</button>
   	  	<div id="school" v-bind:style="maskStyle" class="mask">
   			<div class="place_box">
   	<el-header style="height: 0.88rem;">
  	  <span class="mt-btn" style="float: left;" @click="closeSchool">取消</span>
  	<span>考试地区</span>
  	<span class="mt-btn" style="float: right;" @click="comfirmSchool">确定</span>
  </el-header>
   		 <mt-picker :slots="schoolData"  @change="schoolChange" ref="school"></mt-picker>  
   	</div>
   	</div>
   		<div id="sex"  v-bind:style="maskStyle" class="mask">
   			<div class="place_box">
   	<el-header style="height: 0.88rem;">
  	  <span class="mt-btn" style="float: left;" @click="closeSex">取消</span>
  	<span>考试地区</span>
  	<span class="mt-btn" style="float: right;" @click="comfirmSex">确定</span>
  </el-header>
   		 <mt-picker :slots="sexData"  @change="sexChange" ref="sex"></mt-picker>  
   	</div>
   	</div>
   	
  </el-main>
</el-container>
</template>

<script>
	var height=document.documentElement.clientHeight*2/100+"rem";
	
	export default{
		name:"Wish",
		data:function(){
			return{
				school:this.message.school,
				sex:this.message.sex,
				
				sexData:[{values: ['不限', '男生较多', '女生较多', '男女平均']}],
				schoolData:[{values: ['广东', '北京', '上海', '天津']}],
				maskStyle:{
					height:height
				},
				transfer:{
					
					sex:"",
					school:""
				},
				ProLen:"已选"+this.message.pro.length+"个专业",
				OccLen:"已选"+this.message.occ.length+"个方向"
			}
		},
		mounted(){
			this.sexWidth();
			this.changeDiable();
			if(this.message.sex!=""){
				this.$refs.sex.setSlotValue(0,this.message.sex)
			}
			if(this.message.school!=""){
				this.$refs.school.setSlotValue(0,this.message.school)
			}
			
		},
		
		
		methods:{
			back(){
				this.$router.go(-1);
				this.$router.isBack = true;
			},
			comfirm(){
				this.$router.push({path:'/Analog'})
			},
			goPro(){
				this.$router.push({path:'/Professional'})
			},
			goOcc(){
				this.$router.push({path:'/Occupation'})
			},
			schoolShow(){
				$("#school").fadeIn()
			},
			closeSchool(){
			$("#school").fadeOut()	
			},
			comfirmSchool(){
				this.message.school=this.transfer.school;
				this.school=this.message.school;
					$("#school").fadeOut()	
			},
			schoolChange(picker,values){
				this.transfer.school=values[0];
			},
			sexShow(){
				$("#sex").fadeIn()
			},
			closeSex(){
			$("#sex").fadeOut()	
			},
			comfirmSex(){
				this.message.sex=this.transfer.sex;
				this.sex=this.message.sex;
				  this.sexWidth()
					$("#sex").fadeOut()	
			},
			sexChange(picker,values){
				
				if(this.message.sex){
					
					picker.setSlotValue(1,this.message.sex)
				}
				this.transfer.sex=values[0];
			},
			sexWidth(){
				if(this.sex=="不限"){
					$(".sexBox .select").css("width","1rem")
				}else{
					$(".sexBox .select").css("width","1.5rem")
				}
			}
			,
			changeDiable(){
				if(school!=""&&sex!=""&&this.message.pro.length!=0&&this.message.occ.length!=0){
					$('.confirm_btn').removeAttr("disabled");
					$('.confirm_btn').css("background","#2B94FA")
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
@import "../assets/common.scss";
.confirm_btn{
    background: #8E8E8E;
    
}
</style>


