<template>

  <el-container>
  		<el-header style="height: 0.88rem;">
  	<i class="back" v-on:click="back()"></i>
  	<span>智能填志愿</span>
  </el-header>
  	 <el-main>
  	
  	 		<div class="select_box schoolBox" @click="toMark">
