   		<i class="select_icon"></i>
   		</div>
   	</div>
   	 	<div class="select_box schoolBox" @click="toWish">
   		<span>预估成绩</span>
   		<div class="select" style="width: 2.6rem;">
   		已选{{school}} {{ProLen+OccLen}}项意愿
   		<i class="select_icon"></i>
   		</div>
   	</div>
  	<div class="line"></div>
  	<div class="prompt">
  		<span>为你推荐的学校专业</span>
  		<div @click="prompt">?</div>
  
  	</div>
  	<div class="mark_top" v-if="scrollTop">
