  		<span class="mark_top_right">{{fromPlace}}{{subject}}{{mark}}分</span>
  	</div>
  	 <div id="searchBar" class="nav">  
      <div class="nav_btn "  @click="changeType('tab-container1',0)">可冲击 (0)</div>  
      <div class="nav_btn active"  @click="changeType('tab-container2',1)">较稳妥 (2)</div>  
      <div  class="nav_btn" @click="changeType('tab-container3',2)">可保底 (0)</div> 
      <div class="nav_line"></div>
    </div>  
  	<div class="page-tab-container">  
      <mt-tab-container  class="page-tabbar-tab-container" v-model="active" swipeable='swipeable' ref="swiper" swiping="true" @input="change()">  
        <mt-tab-container-item id="tab-container1" ref="swiper1" @drap="drap">  
        <div class="chance_box">
          	<div class="chance_left">
          		<span class="chance_school">华南理工大学</span><span class="pro">（9个专业推荐）</span>
          		<p class="min_mark">16年一本最低分600</p>
          		<div class="sign_box">
          			<div style="background: #FEA256;">热门学校</div>
          			<div style="background:  #2D7AE3;">平均工资高</div>
          			<div style="background: #FEA256;">热门学校</div>
          		</div>
          	</div>
          	<div class="chance_right">
          		<div class="chart_box">
          			<canvas class="myChart"  width="100%" height="100%" data-total="100" data-curr="70"></canvas>
          			
          		</div>
          	</div>
          </div>
           <div class="chance_box">
          	<div class="chance_left">
          		<span class="chance_school">华南理工大学</span><span class="pro">（9个专业推荐）</span>
          		<p class="min_mark">16年一本最低分600</p>
          		<div class="sign_box">
          			<div style="background: #FEA256;">热门学校</div>
          			<div style="background:  #2D7AE3;">平均工资高</div>
          			<div style="background: #FEA256;">热门学校</div>
          		</div>
          	</div>
          	<div class="chance_right">
          		<div class="chart_box">
          			<canvas class="myChart"  width="100%" height="100%" data-total="100" data-curr="70"></canvas>
          			
          		</div>
          	</div>
          </div>
          
        </mt-tab-container-item>  
        <mt-tab-container-item id="tab-container2" >  
            <!-- cell组件 -->  
        <div class="chance_box">
          	<div class="chance_left">
          		<span class="chance_school">华南理工大学</span><span class="pro">（9个专业推荐）</span>
          		<p class="min_mark">16年一本最低分600</p>
          		<div class="sign_box">
          			<div style="background: #FEA256;">热门学校</div>
          			<div style="background:  #2D7AE3;">平均工资高</div>
          			<div style="background: #FEA256;">热门学校</div>
          		</div>
          	</div>
          	<div class="chance_right">
          		<div class="chart_box">
          			<canvas class="myChart"  width="100%" height="100%" data-total="100" data-curr="70"></canvas>
          			
          		</div>
          	</div>
          </div>
           <div class="chance_box">
          	<div class="chance_left">
          		<span class="chance_school">华南理工大学</span><span class="pro">（9个专业推荐）</span>
          		<p class="min_mark">16年一本最低分600</p>
          		<div class="sign_box">
          			<div style="background: #FEA256;">热门学校</div>
          			<div style="background:  #2D7AE3;">平均工资高</div>
          			<div style="background: #FEA256;">热门学校</div>
          		</div>
          	</div>
          	<div class="chance_right">
          		<div class="chart_box">
          			<canvas class="myChart"  width="100%" height="100%" data-total="100" data-curr="70"></canvas>
          			
          		</div>
          	</div>
          </div>
        </mt-tab-container-item>  
        <mt-tab-container-item id="tab-container3" >  
            <!-- cell组件 -->  
        <div v-for="arrdata in SchoolArr" class="chance_box" @click="goSchool">
          	<div class="chance_left">
          		<span class="chance_school">{{arrdata}}</span><span class="pro">（9个专业推荐）</span>
          		<p class="min_mark">16年一本最低分600</p>
          		<div class="sign_box">
          			<div style="background: #FEA256;">热门学校</div>
          			<div style="background:  #2D7AE3;">平均工资高</div>
          			<div style="background: #FEA256;">热门学校</div>
          					
          		</div>
          	</div>
          	<div class="chance_right">
          		<div class="chart_box">
          			<canvas class="myChart"  width="100%" height="100%" data-total="100" data-curr="70"></canvas>
          			
          		</div>
          	</div>
          </div>
           
        </mt-tab-container-item>  
      </mt-tab-container>  
    </div> 
  	
</el-main>
</el-container>
	
</template>

<script>
 import Vue from 'vue';
	import Zepto from '../../static/zepto.min.js'
	import Progress from '../../static/progress.js'
  import { MessageBox } from 'mint-ui';
let Hub = new Vue();

	let Analog_swiper;
	export default{
		name:"Analog",
		data:function(){
			return {
				mark:this.message.mark,
				ranking:this.message.ranking,
				fromPlace:this.message.fromPlace,
				subject:this.message.subject,
				ProLen:this.message.pro.length,
				OccLen:this.message.occ.length,
				school:this.message.school,
				active:"tab-container1",
				SchoolArr:["华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学","华南理工大学",],
				scrollTop:false,
				id:"001"
			}
		},
		mounted () {
    this.draw()
    console.log(this.$refs.swiper1)
   
    window.addEventListener('scroll', this.handleScroll);
  },
		methods:{
			drap(){
				console.log(this)
			},
			draw(){
				$(".myChart").each(function(){
					$(this).ringChart();
				})
			},
		
			back (){
				this.$router.go(-1);
				this.$router.isBack = true;
			},
			toMark(){
				this.$router.push({path:'/Mark'})
			},
			toWish(){
				this.$router.push({path:'/Wish'})
			},
			prompt(){
				MessageBox.alert("",{
					message:'<p style="font-size: 0.28rem;color: #232427;">可冲击：录取概率48%-80%</p><p style="font-size: 0.28rem;color: #232427;">较稳妥：录取概率80%-98%</p>',
					title:''
				})
			},
			changeType(name,index,e){
		
				this.active=name;
			 $(".nav_btn").eq(index).addClass("active").siblings(".nav_btn").removeClass("active");
			 $(".nav_line").animate({"left":index*33+"%"},200)
			}, 
			change(){
				var index;
				if(this.$refs.swiper.currentActive=="tab-container1"){
					index=0
				}else if(this.$refs.swiper.currentActive=="tab-container2"){
					index=1
				}else{
						index=2
				}
					 $(".nav_btn").eq(index).addClass("active").siblings(".nav_btn").removeClass("active");
			 $(".nav_line").animate({"left":index*33+"%"},200)
				
		
			},
			handleScroll (e) {
				e.preventDefault()
  var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
  var offsetTop = document.querySelector('#searchBar').offsetTop;
 if (scrollTop > offsetTop) {
    this.scrollTop = true
  } else {
    this.scrollTop = false
  }
},
goSchool(){
	  this.$store.state.id =this.id
	this.$router.push({path:'/School'})
}
			
		}
	}
</script>

<style lang="scss" scoped>
    @import "../assets/common.scss";
    @import "../assets/Analog.scss";
	
	
</style>


